import React from "react";
import "./PlanetData.css";

export default function PlanetData(props) {
    return (
        <div className="planet-data-container">
            <div className="planet-data">
                <h3 className="planet-data-key">rotation time</h3>
                <span className="planet-data-value">{props.rotation}</span>
            </div>
            <div className="planet-data">
                <h3 className="planet-data-key">revolution time</h3>
                <span className="planet-data-value">{props.revolution}</span>
            </div>
            <div className="planet-data">
                <h3 className="planet-data-key">radius</h3>
                <span className="planet-data-value">{props.radius}</span>
            </div>
            <div className="planet-data">
                <h3 className="planet-data-key">average temp.</h3>
                <span className="planet-data-value">{props.temperature}</span>
            </div>
        </div>
    );
}
