import React from "react";
import "./PlanetImg.css";

export default function PlanetImg(props) {
    const imgSrc = props.src.slice(8);
    const surface = imgSrc.replace("png", "svg").replace("geology", "planet");

    const styleMobile = {
        mercury: { width: "111px", margin: "95px auto 98px" },
        venus: { width: "154px", margin: "76px auto 76px" },
        earth: { width: "173px", margin: "66px auto 67px" },
        mars: { width: "129px", margin: "90px auto 88px" },
        jupiter: { width: "224px", margin: "40px auto 41px" },
        saturn: { width: "256px", margin: "24px auto 24px" },
        uranus: { width: "176px", margin: "65px auto 65px" },
        neptune: { width: "173px", margin: "64px auto 67px" },
    };

    const styleTablet = {
        mercury: { width: "184px", margin: "146px auto 130px" },
        venus: { width: "253px", margin: "112px auto 95px" },
        earth: { width: "285px", margin: "96px auto 79px" },
        mars: { width: "213px", margin: "132px auto 115px" },
        jupiter: { width: "369px", margin: "54px auto 37px" },
        saturn: { width: "422px", margin: "27px auto 11px" },
        uranus: { width: "290px", margin: "93px auto 77px" },
        neptune: { width: "285px", margin: "96px auto 79px" },
    };

    const styleDesktop = {
        mercury: { width: "290px", margin: "242px auto 222px" },
        venus: { width: "400px", margin: "167px auto 167px" },
        earth: { width: "450px", margin: "162px auto 142px" },
        mars: { width: "336px", margin: "219px auto 199px" },
        jupiter: { width: "582px", margin: "96px auto 76px" },
        saturn: { width: "666px", margin: "70px auto 18px" },
        uranus: { width: "458px", margin: "158px auto 138px" },
        neptune: { width: "450px", margin: "162px auto 142px" },
    };

    const getImgSize = (styleDevice) => {
        for (let planet in styleDevice) {
            if (planet === props.planetName.toLowerCase()) {
                return styleDevice[planet].width;
            }
        }
    };

    const widthStyle = () => {
        if (window.innerWidth > 200 && window.innerWidth < 768) {
            return getImgSize(styleMobile);
        } else if (window.innerWidth >= 768 && window.innerWidth < 1440) {
            return getImgSize(styleTablet);
        } else if (window.innerWidth >= 1440) {
            return getImgSize(styleDesktop);
        }
    };

    const getMargin = (styleDevice) => {
        for (let planet in styleDevice) {
            if (planet === props.planetName.toLowerCase()) {
                return styleDevice[planet].margin;
            }
        }
    };

    const marginStyle = () => {
        if (window.innerWidth > 200 && window.innerWidth < 768) {
            return getMargin(styleMobile);
        } else if (window.innerWidth >= 768 && window.innerWidth < 1440) {
            return getMargin(styleTablet);
        } else if (window.innerWidth >= 1440) {
            return getMargin(styleDesktop);
        }
    };

    const surfaceMargin = () => {
        if (window.innerWidth > 200 && window.innerWidth < 768) {
            return "30%";
        } else if (window.innerWidth >= 768 && window.innerWidth < 1440) {
            return "200px auto 96px";
        } else if (window.innerWidth >= 1440) {
            return "360px 288px 259px";
        }
    }

    return (
        <div className="planet-img-container">
            <img
                className="planet-img"
                style={{
                    width: imgSrc.includes("geology") ? "110px" : widthStyle(),
                    margin: imgSrc.includes("geology") ? surfaceMargin() : marginStyle(),
                    top: imgSrc.includes("geology") ? "60px" : "initial" 
                }}
                src={imgSrc.includes("geology") ? require(`../../assets${imgSrc}`).default : require(`../../assets${surface}`).default}
                alt="planet-img"
            />
            {imgSrc.includes("geology") && (
                <img
                    className="surface-img"
                    style={{
                        width: widthStyle(),
                        margin: marginStyle(),
                    }}
                    src={require(`../../assets${surface}`).default}
                    alt="surface"
                />
            )}
        </div>
    );
}
