import React, { useState } from "react";
import "./App.css";

// * Components
import Navbar from "./components/Navbar/Navbar";
import Tabs from "./components/Tabs/Tabs";
import Planet from "./components/Planet/Planet";
import PlanetInfo from "./components/PlanetInfo/PlanetInfo";

// * Data
import planets from "./data.json";

function App() {
    // * States - Start
    const [activeClass, setActiveClass] = useState({
        activeTab: "overview",
        tab: ["overview", "structure", "surface"],
    });

    const [displayedPlanet, setDisplayedPlanet] = useState(planets[0]);
    // * States - End

    // *********************************************

    // * SetStates - Start
    const changeActiveClass = (index) => {
        setActiveClass({ ...activeClass, activeTab: activeClass.tab[index] });
    };

    const changeDisplayedPlanet = (index) => {
        setDisplayedPlanet(planets[index]);
    };

    // * SetStates - End

    // *********************************************

    const toggleActiveStyle = (index) => {
        if (activeClass.activeTab === activeClass.tab[index]) {
            return "active";
        } else {
            return null;
        }
    };

    const getTabImg = () => {
        if (activeClass.activeTab === "overview") {
            return "planet";
        } else if (activeClass.activeTab === "structure") {
            return "internal";
        } else if (activeClass.activeTab === "surface") {
            return "geology";
        }
    };

    const getPlanetDescription = () => {
        if (activeClass.activeTab === "overview") {
            return displayedPlanet.overview.content;
        } else if (activeClass.activeTab === "structure") {
            return displayedPlanet.structure.content;
        } else if (activeClass.activeTab === "surface") {
            return displayedPlanet.geology.content;
        }
    };

    const getPlanetWikipediaLink = () => {
        if (activeClass.activeTab === "overview") {
            return displayedPlanet.overview.source;
        } else if (activeClass.activeTab === "structure") {
            return displayedPlanet.structure.source;
        } else if (activeClass.activeTab === "surface") {
            return displayedPlanet.geology.source;
        }
    };

    const planetsColors = {
        mercury: "#DEF4FC",
        venus: "#F7CC7F",
        earth: "#545BFE",
        mars: "#FF6A45",
        jupiter: "#ECAD7A",
        saturn: "#FCCB6B",
        uranus: "#65F0D5",
        neptune: "#497EFA",
    };

    return (
        <div className="App">
            <Navbar
                displayedPlanet={displayedPlanet.name}
                changeDisplayedPlanet={changeDisplayedPlanet}
                colors={planetsColors}
            />
            <Tabs
                displayedPlanet={displayedPlanet}
                activeClass={activeClass}
                onClick={changeActiveClass}
                toggleActiveStyle={toggleActiveStyle}
                color={planetsColors}
            />
            <Planet
                imgSrc={displayedPlanet.images[getTabImg()]}
                planetName={displayedPlanet.name}
                planetDescription={getPlanetDescription()}
                link={getPlanetWikipediaLink()}
            />
            <PlanetInfo displayedPlanet={displayedPlanet} />
        </div>
    );
}

export default App;
