import React from "react";
import PlanetData from "../PlanetData/PlanetData";

import "./PlanetInfo.css";

export default function PlanetInfo(props) {
    return (
        <section style={{gridColumn: "1 / 3"}}>
            <PlanetData
                rotation={props.displayedPlanet.rotation}
                revolution={props.displayedPlanet.revolution}
                radius={props.displayedPlanet.radius}
                temperature={props.displayedPlanet.temperature}
            />
        </section>
    );
}
