import React from "react";
import "./NavList.css";
import planets from "../../data.json";

export default function NavList(props) {
    const handleClick = (index) => {
        props.changeDisplayedPlanet(index);
        props.closeMenu();
        document.body.style.overflowY = "initial"
    };

    const topBorder = (planet) => (
        <div
            className="top-border"
            style={{
                borderTop: `3px solid ${
                    props.colors[planet.name.toLowerCase()]
                }`,
            }}
        ></div>
    );

    return (
        <div className={`NavList ${props.className}`}>
            {planets.map((planet, index) => {
                return (
                    <div
                        key={index}
                        onClick={() => handleClick(index)}
                        className="nav__item"
                    >
                        {window.innerWidth < 768 && (
                            <div
                                className="nav__bullet"
                                style={{
                                    backgroundColor:
                                        props.colors[planet.name.toLowerCase()],
                                }}
                            ></div>
                        )}
                        {window.innerWidth >= 1440 && props.displayedPlanet === planet.name && topBorder(planet)}
                        <div className="nav__planet">{planet.name}</div>
                    </div>
                );
            })}
        </div>
    );
}
