import React from "react";
import styled, { keyframes } from "styled-components";

// * Components
import PlanetImg from "../PlanetImg/PlanetImg";
import PlanetDescription from "../PlanetDescription/PlanetDescription";

// * Styles
import "./Planet.css";

export default function Planet(props) {
    return (
        <section className="main-section">
            <PlanetImg src={props.imgSrc} planetName={props.planetName} />
            <PlanetDescription
                planetName={props.planetName}
                planetDescription={props.planetDescription}
                linkSrc={props.link}
            />
        </section>
    );
}
