import React from "react";
import "./Tabs.css";
import "../../reset.css";

export default function Tabs(props) {
    const changeLeft = (tab) => {
        if (tab === "overview") {
            return "6.5%";
        } else if (tab === "structure") {
            return "39.5%";
        } else if (tab === "surface") {
            return "73%";
        }
    };

    const planetsColorsTabletDesktop = {
        mercury: "#419EBB",
        venus: "#EDA249",
        earth: "#6D2ED5",
        mars: "#D14C32",
        jupiter: "#D83A34",
        saturn: "#CD5120",
        uranus: "#1EC1A2",
        neptune: "#2D68F0",
    }

    const changeBackgroundColor = () => {
        if (window.innerWidth > 767) {
            return planetsColorsTabletDesktop[props.displayedPlanet.name.toLowerCase()]
        }
    }

    return (
        <div className="tabs">
            {window.innerWidth < 768 && (
                <div
                    className="border"
                    style={{
                        backgroundColor:
                            props.color[
                                props.displayedPlanet.name.toLowerCase()
                            ],
                        left: changeLeft(props.activeClass.activeTab),
                    }}
                ></div>
            )}
            {props.activeClass.tab.map((tab, index) => {
                return (
                    <h2
                        key={index}
                        onClick={() => props.onClick(index)}
                        className={`tab ${props.toggleActiveStyle(index)}`}
                        style={{backgroundColor: tab === props.activeClass.activeTab && changeBackgroundColor()}}
                    >
                        {tab}
                    </h2>
                );
            })}
        </div>
    );
}
