import React from "react";
import "./PlanetDescription.css";

export default function PlanetDescription(props) {
    return (
        <article className="description-container">
            <h1 className="planet-name">{props.planetName}</h1>
            <h2 className="planet-description">{props.planetDescription}</h2>
            <span className="source">
                Source :
                <a className="source-link" href={props.linkSrc}>Wikipedia</a>
            </span>
        </article>
    );
}
